import * as React from "react";
import Table from "@js/common/table/index";
import {NavLink} from "react-router-dom";
import {Button} from "@js/common/Button"
// pageNum pageSize role_idid status:0 status,status_name 356 1:24 fdept  fsex  fage  lrdata  gddata   code 200 message data total list pageNum pageSize prePage nextPage navigatepageNums

type ListItem = {
    id: string;
    fname: string;
    fsex: "" | "",
    fage: string;
    fdept: string;
    fdeb: string;
    fprn: string;
    fsurvey: string;
    fryqk: string;
    fryzd: string;
    fzljg: string;
    fcyqk: string;
    fcyyz: string;
    status: string;
    status_name: string;
    lrdata: string;
    disp: string;
}

type caseData = {
    list:ListItem[];
    pageNum: number;
    pageSize: number;
    size: number;
    startRow: number;
    endRow: number;
    pages: number;
    prePage: number;
    nextPage: number;
    isFirstPage: boolean;
    isLastPage: boolean;
    hasPreviousPage: boolean;
    hasNextPage: boolean;
    navigatePages: number;
    navigatepageNums: number[];
    navigateFirstPage: number;
    navigateLastPage: number;
    lastPage: number;
    firstPage: number;
    total:number;
}



   
type ResultProp = {
    changeHandle(field:any,value:string):void;
    data:caseData;
    type : string; 
    delItem:(ids:string)=>void;
    roleId:string;
} 
type ResultState={
}

class ResultSearch extends React.PureComponent<ResultProp,ResultState>{

    column =   [
        {
            text: "",
            field: "fname",
            width: 180,
        },
        {
            text: "",
            field: "fsex",
        },
        {
            text: "",
            field: "fage",
        },
        {
            text: "",
            field: "fdept",
        },
        {
            text: "",
            field: "fdeb",
        },
        {
            text: "",
            field: "fprn",
        },
        {
            text: "", 
            field: "lrdata",
        },
         {
            text: "", 
            field: "gddata",
        },
        {
            text: "",
            field: "status_name",
            formatter:function(node:any){

                //  356 1:24
                const status = node.status;

                const obj={
                    "1":"m-translate-warn",
                    "2":"m-translate-warn",
                    "3":"m-translate-green",
                    "4":"m-translate-error",
                    "5":"",
                    "6":"m-translate-error",
                }
                const name = obj[status as "1"];
                return <span className={name}>{node.status_name}</span>; 

            }
        },
        {
            text: "",
            field: "opt",
            width: 140,
            formatter: (node:any)=>{

                const { type ,roleId} = this.props;
                const pathObj = {
                    pathname:"/translate",
                    state:{
                        id:node.id,
                        type,
                    }
                }
                const name = this.props.roleId == "3202" ? "" :  "";
                return (<>
                        <NavLink to={pathObj} ><button className="s-btn normal-btn primary" >{name}</button></NavLink>
                        &nbsp;
                       {roleId != "3202" ? <Button type="danger" field={node.id}  handle={this.delItemCase}></Button> :null}
                        </>
                        )
            }
        }
    ];

    state={
  
    }

    delItemCase=(e:React.MouseEvent<HTMLButtonElement>)=>{


        const id = e.currentTarget!.name;

        this.props.delItem(id);
    }
    componentDidMount(){

       

        if(this.props.type =="/summary"){

            this.column.splice(7,1)

        }

    }
    render(){
        const {data,roleId} = this.props;

        if(!data){
            return "";
        }

        const {list,pageNum,pageSize,total,pages} = data;
        const {changeHandle} = this.props;
        
            return <Table 
                        list={list} 
                        column={this.column}
                        pageNum={pageNum}
                        pageSize={pageSize}
                        total={total}
                        pages={pages}
                        changeHandle={changeHandle}
                        checkbox={roleId!="3202"}
                    />


    }
   

}

export default ResultSearch;

